.App {
  text-align: center;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-header h1 {
  margin: 0;
  font-size: 3rem;
  color: #61dafb;
}

.App-header p {
  margin-top: 1rem;
  font-size: 1.2rem;
  color: #ffffff;
}
